import React, { useState, useEffect } from "react";
import "../assets/styles/pages.scss";
import { useContractRead, useContractWrite, useWaitForTransaction } from 'wagmi';
import { useAccount } from 'wagmi';
import "../hide.css";
import { abi } from "../constants/abi";

function Hero(props) {
  const { address } = useAccount();

  // Define the state variables for inputs
  const [name, setName] = useState('');
  const [productId, setProductId] = useState('');
  const [newState, setNewState] = useState('');
  const [id, setId] = useState('');
  const [description, setDescription] = useState('');
  const [newStatus, setNewStatus] = useState('');
  const [amount, setAmount] = useState('');
    const [productState, setProductState] = useState('');
  // State variables for tracking status and errors
  const [createProductStatus, setCreateProductStatus] = useState(null);
  const [updateProductStateStatus, setUpdateProductStateStatus] = useState(null);
  const [transferOwnershipStatus, setTransferOwnershipStatus] = useState(null);
  const [registerMaterialStatus, setRegisterMaterialStatus] = useState(null);
  const [updateMaterialStatusStatus, setUpdateMaterialStatusStatus] = useState(null);
  const [verifySupplierStatus, setVerifySupplierStatus] = useState(null);
  const [executePaymentStatus, setExecutePaymentStatus] = useState(null);

  const [productIdRead, setProductIdRead] = useState('');
  const [materialId, setMaterialId] = useState('');

  const { data: product, isError: isProductError, isLoading: isProductLoading, error: productError } = useContractRead({
    address: "0x422CA5bC74486151846a29489b72Dad4c47886E2",
    abi: abi,
    functionName: 'products',
    args: [productIdRead],
  });

  useEffect(() => {
    if (product) {
      console.log("Product Data:", product);
    }

    const states = [
        "Produced",
        "Processed",
        "Packaged",
        "ForSale",
        "Sold",
        "Shipped",
        "Received"
    ]

    if (product && product[2] == 0) {
        setProductState(states[0])
    }
    if (product && product[2] == 1) {
        setProductState(states[1])
    }
    if (product && product[2] == 2) {
        setProductState(states[2])
    }
    if (product && product[2] == 3) {
        setProductState(states[3])
    }
    if (product && product[2] == 4) {
        setProductState(states[4])
    }
    if (product && product[2] == 5) {
        setProductState(states[5])
    }
    if (product && product[2] == 6) {
        setProductState(states[6])
    }
  }, [product]);

  const { data: material, isError: isMaterialError, isLoading: isMaterialLoading, error: materialError } = useContractRead({
    address: "0x422CA5bC74486151846a29489b72Dad4c47886E2",
    abi: abi,
    functionName: 'getMaterial',
    args: [materialId],
  });

  // Create Product
  const { data: createProductData, isLoading: createProductLoading, error: createProductError, write: createProductWrite } = useContractWrite({
    address: "0x422CA5bC74486151846a29489b72Dad4c47886E2",
    abi: abi,
    functionName: 'createProduct',
    onSuccess: () => setCreateProductStatus('Success'),
    onError: (err) => setCreateProductStatus(err.message),
  });

  // Update Product State
  const { isLoading: updateProductStateLoading, error: updateProductStateError, write: updateProductStateWrite } = useContractWrite({
    address: '0x422CA5bC74486151846a29489b72Dad4c47886E2',
    abi: abi,
    functionName: 'updateProductState',
    onSuccess: () => setUpdateProductStateStatus('Success'),
    onError: (err) => setUpdateProductStateStatus(err.message),
  });

  // Transfer Product Ownership
  const { isLoading: transferProductOwnershipLoading, error: transferProductOwnershipError, write: transferProductOwnershipWrite } = useContractWrite({
    address: '0x422CA5bC74486151846a29489b72Dad4c47886E2',
    abi: abi,
    functionName: 'transferProductOwnership',
    args: [productId, address],
    onSuccess: () => setTransferOwnershipStatus('Success'),
    onError: (err) => setTransferOwnershipStatus(err.message),
  });

  // Register Material
  const { data: registerMaterialData, isLoading: registerMaterialLoading, error: registerMaterialError, write: registerMaterialWrite } = useContractWrite({
    address: "0x422CA5bC74486151846a29489b72Dad4c47886E2",
    abi: abi,
    functionName: 'registerMaterial',
    onSuccess: () => setRegisterMaterialStatus('Success'),
    onError: (err) => setRegisterMaterialStatus(err.message),
  });

  // Update Material Status
  const { isLoading: updateMaterialStatusLoading, error: updateMaterialStatusError, write: updateMaterialStatusWrite } = useContractWrite({
    address: '0x422CA5bC74486151846a29489b72Dad4c47886E2',
    abi: abi,
    functionName: 'updateMaterialStatus',
    onSuccess: () => setUpdateMaterialStatusStatus('Success'),
    onError: (err) => setUpdateMaterialStatusStatus(err.message),
  });

  // Verify Supplier
  const { isLoading: verifySupplierLoading, error: verifySupplierError, write: verifySupplierWrite } = useContractWrite({
    address: '0x422CA5bC74486151846a29489b72Dad4c47886E2',
    abi: abi,
    functionName: 'verifySupplier',
    onSuccess: () => setVerifySupplierStatus('Success'),
    onError: (err) => setVerifySupplierStatus(err.message),
  });

  // Execute Payment
  const { isLoading: executePaymentLoading, error: executePaymentError, write: executePaymentWrite } = useContractWrite({
    address: '0x422CA5bC74486151846a29489b72Dad4c47886E2',
    abi: abi,
    functionName: 'executePayment',
    args: [id, amount],
    onSuccess: () => setExecutePaymentStatus('Success'),
    onError: (err) => setExecutePaymentStatus(err.message),
  });

  // Handle form submissions
  const handleCreateProduct = (e) => {
    e.preventDefault();
    createProductWrite({ args: [name] });
  };

  const handleUpdateProductState = (e) => {
    e.preventDefault();
    updateProductStateWrite({ args: [productId, newState] });
  };

  const handleTransferProductOwnership = (e) => {
    e.preventDefault();
    transferProductOwnershipWrite();
  };

  const handleRegisterMaterial = (e) => {
    e.preventDefault();
    registerMaterialWrite({ args: [id, description] });
  };

  const handleUpdateMaterialStatus = (e) => {
    e.preventDefault();
    updateMaterialStatusWrite({ args: [id, newStatus] });
  };

  const handleVerifySupplier = (e) => {
    e.preventDefault();
    verifySupplierWrite({ args: [id] });
  };

  const handleExecutePayment = (e) => {
    e.preventDefault();
    executePaymentWrite();
  };

  return (
    <div className="container mt-5 suppply">
      <h2>Supply Chain Management</h2>

{/* Product Section */}
<div className="mb-4">
        <div className="mb-3">
          <input
            type="text"
            className="form-control"
            placeholder="Enter Product ID"
            value={productIdRead}
            onChange={(e) => setProductIdRead(e.target.value)}
          />
        </div>
        {isProductLoading && <div>Loading...</div>}
        {isProductError && <div className="alert alert-danger">Error: {productError?.message}</div>}
        {product && (
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">Product ID: {productIdRead}</h5>
              <p className="card-text"><strong>Name:</strong> {product[1]}</p>
              <p className="card-text"><strong>State:</strong> {productState}</p>
              <p className="card-text"><strong>Owner:</strong> {product[3]}</p>
            </div>
          </div>
        )}
      </div>

      {/* Material Section */}
      <div className="mb-4">
        <div className="mb-3">
          <input
            type="text"
            className="form-control"
            placeholder="Enter Material ID"
            value={materialId}
            onChange={(e) => setMaterialId(e.target.value)}
          />
        </div>
        {isMaterialLoading && <div>Loading...</div>}
        {isMaterialError && <div className="alert alert-danger">Error: {materialError?.message}</div>}
        {material && (
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">Material ID: {material.id}</h5>
              <p className="card-text"><strong>Description:</strong> {material.description}</p>
              <p className="card-text"><strong>Supplier:</strong> {material.supplier}</p>
              <p className="card-text"><strong>Verified:</strong> {material.isVerified ? "Yes" : "No"}</p>
              <p className="card-text"><strong>Status:</strong> {material.status}</p>
            </div>
          </div>
        )}
      </div>

      {/* Create Product */}
      <div className="card mt-4">
        <div className="card-header">Create Product</div>
        <div className="card-body">
          <form id="createProductForm" onSubmit={handleCreateProduct}>
            <div className="mb-3">
              <label htmlFor="productName" className="form-label">Product Name</label>
              <input type="text" className="form-control" id="productName" placeholder="Enter product name"
                value={name} onChange={(e) => setName(e.target.value)} />
            </div>
            <button type="submit" className="btn btn-primary" disabled={createProductLoading}>Create Product</button>
            {createProductError && <p className="text-danger">Error: {createProductError.message}</p>}
            {createProductStatus && <p>Status: {createProductStatus}</p>}
          </form>
        </div>
      </div>

      {/* Update Product State */}
      <div className="card mt-4">
        <div className="card-header">Update Product State</div>
        <div className="card-body">
          <form id="updateProductStateForm" onSubmit={handleUpdateProductState}>
            <div className="mb-3">
              <label htmlFor="productId" className="form-label">Product ID</label>
              <input type="number" className="form-control" id="productId" placeholder="Enter product ID"
                value={productId} onChange={(e) => setProductId(e.target.value)} />
            </div>
            <div className="mb-3">
              <label htmlFor="productState" className="form-label">New State</label>
              <select className="form-select" id="productState" value={newState} onChange={(e) => setNewState(e.target.value)}>
                <option value="0">Produced</option>
                <option value="1">Processed</option>
                <option value="2">Packaged</option>
                <option value="3">ForSale</option>
                <option value="4">Sold</option>
                <option value="5">Shipped</option>
                <option value="6">Received</option>
              </select>
            </div>
            <button type="submit" className="btn btn-primary" disabled={updateProductStateLoading}>Update State</button>
            {updateProductStateError && <p className="text-danger">Error: {updateProductStateError.message}</p>}
            {updateProductStateStatus && <p>Status: {updateProductStateStatus}</p>}
          </form>
        </div>
      </div>

      {/* Transfer Product Ownership */}
      <div className="card mt-4">
        <div className="card-header">Transfer Product Ownership</div>
        <div className="card-body">
          <form id="transferProductOwnershipForm" onSubmit={handleTransferProductOwnership}>
            <div className="mb-3">
              <label htmlFor="productIdTransfer" className="form-label">Product ID</label>
              <input type="number" className="form-control" id="productIdTransfer" placeholder="Enter product ID"
                value={productId} onChange={(e) => setProductId(e.target.value)} />
            </div>
            <div className="mb-3">
              <label htmlFor="newOwner" className="form-label">New Owner Address</label>
              <input type="text" className="form-control" id="newOwner" placeholder="Enter new owner address"
                value={address} readOnly />
            </div>
            <button type="submit" className="btn btn-primary" disabled={transferProductOwnershipLoading}>Transfer Ownership</button>
            {transferProductOwnershipError && <p className="text-danger">Error: {transferProductOwnershipError.message}</p>}
            {transferOwnershipStatus && <p>Status: {transferOwnershipStatus}</p>}
          </form>
        </div>
      </div>

      {/* Register Material */}
      <div className="card mt-4">
        <div className="card-header">Register Material</div>
        <div className="card-body">
          <form id="registerMaterialForm" onSubmit={handleRegisterMaterial}>
            <div className="mb-3">
              <label htmlFor="materialId" className="form-label">Material ID</label>
              <input type="text" className="form-control" id="materialId" placeholder="Enter material ID"
                value={id} onChange={(e) => setId(e.target.value)} />
            </div>
            <div className="mb-3">
              <label htmlFor="materialDescription" className="form-label">Material Description</label>
              <input type="text" className="form-control" id="materialDescription" placeholder="Enter material description"
                value={description} onChange={(e) => setDescription(e.target.value)} />
            </div>
            <button type="submit" className="btn btn-primary" disabled={registerMaterialLoading}>Register Material</button>
            {registerMaterialError && <p className="text-danger">Error: {registerMaterialError.message}</p>}
            {registerMaterialStatus && <p>Status: {registerMaterialStatus}</p>}
          </form>
        </div>
      </div>

      {/* Verify Supplier */}
      <div className="card mt-4">
        <div className="card-header">Verify Supplier</div>
        <div className="card-body">
          <form id="verifySupplierForm" onSubmit={handleVerifySupplier}>
            <div className="mb-3">
              <label htmlFor="materialIdVerify" className="form-label">Material ID</label>
              <input type="text" className="form-control" id="materialIdVerify" placeholder="Enter material ID"
                value={id} onChange={(e) => setId(e.target.value)} />
            </div>
            <button type="submit" className="btn btn-primary" disabled={verifySupplierLoading}>Verify Supplier</button>
            {verifySupplierError && <p className="text-danger">Error: {verifySupplierError.message}</p>}
            {verifySupplierStatus && <p>Status: {verifySupplierStatus}</p>}
          </form>
        </div>
      </div>

      {/* Execute Payment */}
      <div className="card mt-4">
        <div className="card-header">Execute Payment</div>
        <div className="card-body">
          <form id="executePaymentForm" onSubmit={handleExecutePayment}>
            <div className="mb-3">
              <label htmlFor="amount" className="form-label">Amount</label>
              <input type="number" className="form-control" id="amount" placeholder="Enter amount"
                value={amount} onChange={(e) => setAmount(e.target.value)} />
            </div>
            <button type="submit" className="btn btn-primary" disabled={executePaymentLoading}>Execute Payment</button>
            {executePaymentError && <p className="text-danger">Error: {executePaymentError.message}</p>}
            {executePaymentStatus && <p>Status: {executePaymentStatus}</p>}
          </form>
        </div>
      </div>
    </div>
  );
}

export default Hero;
